import React, { useState } from "react";
import "./Tab.css";

export default function Tab() {
  const [activeIndex, setActiveIndex] = useState(0);

  const tabClickHandler = (index) => {
    setActiveIndex(index);
  };

  const tabContArr = [
    {
      tabTitle: (
        <li
          id="tabs-list"
          className={activeIndex === 0 ? "is-active" : ""}
          onClick={() => tabClickHandler(0)}
        >
           
        </li>
      ),
      tabCont: (
        <div className="tabContent">
          <p className="using-phoneNumber"> </p>
          <form className="find-id-form find-form">
            <ul>
              <li>
                <label className="input-title" name="user-name">
                  
                </label>
                <input placeholder=" ." name="user-name" />
              </li>
              <li>
                <label className="input-title" name="user-birth">
                  
                </label>
                <input placeholder=" 8 ." />
              </li>
              <li>
                <label className="input-title" name="user-email">
                  
                </label>
                <input placeholder="  ." />
              </li>
              <button
                className="confirm-btn"
                style={{
                  fontFamily: "Gowun Batang",
                }}
              >
                
              </button>
            </ul>
          </form>
        </div>
      ),
    },
    {
      tabTitle: (
        <li
          id="tabs-list"
          className={activeIndex === 1 ? "is-active" : ""}
          onClick={() => tabClickHandler(1)}
        >
           
        </li>
      ),
      tabCont: (
        <div className="tabContent">
          <p className="using-phoneNumber"> </p>
          <form className="find-password-form find-form">
            <ul>
              <li>
                <label className="input-title" name="user-id">
                  
                </label>
                <input placeholder=" ." name="user-id" />
              </li>
              <li>
                <label className="input-title" name="user-name">
                  
                </label>
                <input placeholder=" ." name="user-name" />
              </li>
              <li>
                <label className="input-title" name="user-birth">
                  
                </label>
                <input placeholder=" 8 ." />
              </li>
              <li>
                <label className="input-title" name="user-email">
                  
                </label>
                <input placeholder="  ." />
              </li>
              <button
                className="confirm-btn"
                style={{
                  fontFamily: "Gowun Batang",
                }}
              >
                
              </button>
            </ul>
          </form>
        </div>
      ),
    },
  ];

  return (
    <div>
      <ul className="tabs is-boxed find-tabs">
        {tabContArr.map((section, index) => {
          return section.tabTitle;
        })}
      </ul>
      <div>{tabContArr[activeIndex].tabCont}</div>
    </div>
  );
}
