import "../components/default.css";
import "../components/reviewWritePage.css";
import bookImg from "../images/bookImage_manglegom.jpg";

function ReviewWritePage() {
  return (
    <>
      <div className="reviewWrite-contents-wrap">
        <h1 className="reviewWrite-contents-tit"> </h1>
        <div className="reviewWrite-book-info">
          <p className="reviewWrite-book-img">
            <img src={bookImg} />
          </p>
          <div className="review-bookName-box">
            <p className="review-bookName"> </p>
          </div>
          <div className="review-book-rating">  </div>
        </div>
        <form id="reviewWrite-form">
          <textarea
            id="reviewWrite-form-textarea"
            placeholder="10  .   , ,       ."
          ></textarea>
          <button
            type="submit"
            id="reviewWrite-form-btn"
            style={{ fontFamily: "Gowun Batang" }}
          >
            
          </button>
        </form>
      </div>
    </>
  );
}

export default ReviewWritePage;
